

.grid{
    max-width: 1180px;
    width:100%;
    margin:0 auto;
    background: rgba(66, 62, 62, 0.2);
    height: 885px;
    font-size: 0px;
    margin-top:60px;
    
    
    .coll{
        max-width: 25%;
        width: 100%;
        display: inline-block;
        vertical-align: top;
        height: 100%;
        overflow:hidden;
      
     @media screen and(max-width:1299px){
       .grid{
         max-width:50%;
         width:100%;        
        }
      }
      
      .square{
        height: 295px;
        width: 100%;
        border: 1px solid silver;
        color: white;
        overflow:hidden;
        &:nth-of-type(even){
        background: rgba(70, 68, 68, 0.548);                    
      }
      img{
       // position:relative;       
      }

      &:nth-child(3){
        background: black;
      }

        article{
          position:relative;
          top:50%;
          transform: translatey(-50%);
          width: 250px;
          margin: 0 auto; 
          text-align:center;
          line-height: 25px;
             
        }
        &.big{
         height: 590px;
         background: url(../pictures/citynight1.jpg)
         
        }
        h1{
            font-size: 25px;
            font-family: $primary-font;
            text-transform: uppercase;
            
        }
        p{
            font-family: $copy-font;
        }
        img{
          width:100%;
          height: 100%;
        }
        p:nth-of-type(even){
            font-size: 25px;           
        }
        p:nth-of-type(odd){
            font-size: 15px;     
        } 
        figure {
          text-align:center;
          display:inline-block;
          padding:0 60px;
          position:relative;
          &:before {
            content:'';
            display:block;
            width:40px;
            height:2px;
            background:#000;
            left:0;
            top:18px;
            position:absolute;
          }
          &:after {
            content:'';
            display:block;
            width:40px;
            height:2px;
            background:#000;
            right:0;
            top:18px;
            position:absolute;
          }
          img {
            width:25px;
            padding-bottom:10px,
          }
          
         }
      }
    }
  }

  